  <div class="dashboard" role="main" aria-labelledby="dashboard-title">
    <!-- Page Header -->
    <header class="page-header">
      <div class="page-header-content">
        <h1 id="dashboard-title">Dashboard de Inventrio</h1>
        <p>Viso geral do inventrio de ativos</p>
      </div>
      <div class="page-actions">
        <button 
          class="btn btn-primary" 
          @click="refreshData"
          :disabled="loading"
          :aria-label="loading ? 'Atualizando dados...' : 'Atualizar dados do dashboard'"
          @keydown.enter="refreshData"
          @keydown.space.prevent="refreshData"
        >
          <i class="fas fa-sync-alt" :class="{ 'fa-spin': loading }" aria-hidden="true"></i>
          <span>{{ loading ? 'Atualizando...' : 'Atualizar' }}</span>
        </button>
      </div>
    </header>

    <!-- KPI Cards -->
    <section class="kpi-section" aria-labelledby="kpi-section-title">
      <h2 id="kpi-section-title" class="sr-only">Indicadores Principais de Performance</h2>
      <div class="kpi-grid" role="group" aria-label="Cartes de mtricas principais">
        <article 
          class="kpi-card" 
          role="button"
          tabindex="0"
          @click="navigateToAssets"
          @keydown.enter="navigateToAssets"
          @keydown.space.prevent="navigateToAssets"
          aria-label="Total de ativos: {{ totalAssets }}. Clique para ver detalhes dos ativos"
        >
          <div class="kpi-icon assets" aria-hidden="true">
            <i class="fas fa-boxes"></i>
          </div>
          <div class="kpi-content">
            <h3 aria-label="Nmero total de ativos">{{ totalAssets }}</h3>
            <p>Total de Ativos</p>
          </div>
        </article>

        <article 
          class="kpi-card"
          role="button"
          tabindex="0"
          @click="navigateToFinancial"
          @keydown.enter="navigateToFinancial"
          @keydown.space.prevent="navigateToFinancial"
          aria-label="Valor total dos ativos: {{ formatCurrency(normalizedTotalValue) }}. Clique para ver relatrios financeiros"
        >
          <div class="kpi-icon value" aria-hidden="true">
            <i class="fas fa-dollar-sign"></i>
          </div>
          <div class="kpi-content">
            <h3 aria-label="Valor total dos ativos">{{ formatCurrency(normalizedTotalValue) }}</h3>
            <p>Valor Total</p>
          </div>
        </article>

        <article 
          class="kpi-card"
          role="button"
          tabindex="0"
          @click="navigateToActiveAssets"
          @keydown.enter="navigateToActiveAssets"
          @keydown.space.prevent="navigateToActiveAssets"
          aria-label="Ativos ativos: {{ activeAssets }}. Clique para ver ativos em uso"
        >
          <div class="kpi-icon active" aria-hidden="true">
            <i class="fas fa-check-circle"></i>
          </div>
          <div class="kpi-content">
            <h3 aria-label="Nmero de ativos ativos">{{ activeAssets }}</h3>
            <p>Ativos Ativos</p>
          </div>
        </article>

        <article 
          class="kpi-card"
          role="button"
          tabindex="0"
          @click="navigateToMaintenance"
          @keydown.enter="navigateToMaintenance"
          @keydown.space.prevent="navigateToMaintenance"
          aria-label="Ativos em manuteno: {{ maintenanceCount }}. Clique para ver ordens de servio"
        >
          <div class="kpi-icon maintenance" aria-hidden="true">
            <i class="fas fa-tools"></i>
          </div>
          <div class="kpi-content">
            <h3 aria-label="Nmero de ativos em manuteno">{{ maintenanceCount }}</h3>
            <p>Em Manuteno</p>
          </div>
        </article>
      </div>
    </section>

    <!-- Charts Section -->
    <section class="charts-section" aria-labelledby="charts-section-title">
      <h2 id="charts-section-title" class="sr-only">Grficos e Anlises</h2>
      <div v-if="!loading" class="charts-grid" role="group" aria-label="Grficos de anlise de dados">
        <!-- Line Chart - Tendncias -->
        <div class="chart-container" role="img" aria-labelledby="trends-chart-title">
          <SimpleLineChart
            title="Tendncias de Ativos"
            :datasets="trendsData"
            :labels="trendsLabels"
            :width="400"
            :height="250"
          />
        </div>

        <!-- Pie Chart - Status -->
        <div class="chart-container" role="img" aria-labelledby="status-chart-title">
          <SimplePieChart
            title="Distribuio por Status"
            :data="statusData"
            :size="250"
            :donut="true"
            :show-center-text="true"
          />
        </div>

        <!-- Bar Chart - Categorias -->
        <div class="chart-container" role="img" aria-labelledby="category-chart-title">
          <SimpleBarChart
            title="Ativos por Categoria"
            :datasets="categoryData"
            :labels="categoryLabels"
            :width="400"
            :height="250"
          />
        </div>

        <!-- Bar Chart - Departamentos -->
        <div class="chart-container" role="img" aria-labelledby="department-chart-title">
          <SimpleBarChart
            title="Ativos por Departamento"
            :datasets="departmentData"
            :labels="departmentLabels"
            :width="400"
            :height="250"
          />
        </div>

      </div>

      <div v-else>
        <SkeletonLoader :rows="4" :columns="2" :rowHeight="220" />
      </div>
      </section>

    <!-- Project Quality Monitor -->
    <ProjectQualityMonitor />

    <!-- Recent Activities -->
    <div class="activities-section">
      <div class="card">
        <div class="card-header">
          <h3>Atividades Recentes</h3>
          <router-link to="/movements" class="view-all-link">
            Ver todas
          </router-link>
        </div>
        <div class="card-body">
          <div class="activity-list">
            <div 
              v-for="activity in recentActivities" 
              :key="activity.id"
              class="activity-item"
            >
              <div class="activity-icon" :class="activity.type">
                <i :class="activity.icon"></i>
              </div>
              <div class="activity-content">
                <p class="activity-title">{{ activity.title }}</p>
                <p class="activity-description">{{ activity.description }}</p>
                <span class="activity-time">{{ formatTime(activity.time) }}</span>
              </div>
            </div>
          </div>
